.header {
  width: 80%;
  margin: 10px auto;
  background-color: #222;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.header-link {
  color: #ddd;
  padding: 10px;
  margin: 5px;

  text-decoration: none;
  border-radius: 5px;
}

.header-link:hover {
  color: #fff;
  background-color: #444;
  text-decoration: none;
}

.header-link-inactive {
  color: #ddd;
  padding: 10px;
  margin: 5px;
  background-color: #444;

  text-decoration: none;
  border-radius: 5px;

  pointer-events: none;
  cursor: default;
}



